import { LitElement, html, css } from "lit-element";


class GameD extends LitElement {
  static get properties() {
    return {
    };
  }

  constructor() {
    super();
  }

  static get styles() {
    return [css`/*Mobile first*/ */
      .my-video{
        width: 120px;
        height: 95px;
      }
      .video-cont{
        margin-left: -40px;
      }
      @media screen and (min-width: 600px) {
        .my-video{
          width: 620px;
          height: 315px;
        }
        .video-cont{
          margin-left: 0px;
        }
      }
    `];
  }

  render() {
    return html`
      <div>
      Tonality</br>
        <ul>
          <li>There are three different tonalities when expressing vocally and here
            you'll see Todd explaining them</br>
            <div class="video-cont">
              <iframe class="my-video" width="280"
                src="https://www.youtube.com/embed/-6V-G1CE1xc?start=422">
              </iframe>
            </div></br>
          </li>
          <li>Todd's suggestied exercises is here</br>
            <div class="video-cont">
              <iframe class="my-video" width="280"
                src="https://www.youtube.com/embed/MuHauLVM5Z0?start=1237">
              </iframe>
            </div></br>
          </li>
          <li>My suggestion is similar to his but with a twist to make it more effective</li>
          <li>I want you to:
            <ul>
              <li>Take your opener/favourite game expression</li>
              <li>Think of how you'd say it with a cocky style, including insults or exaggeration expression in it</li>
              <li>Say it outloud WITH a smile on your face</li>
              <li>Say it once pronouncing the insults </br>and after say it again with a emphasised pause instead harsh remarks
                Examples:
                </br>Who [pause instead of 'the fuck'] are you?
                </br>Hi [pause instead 'darling/honey/bitch'], I am YOUR NAME [pause instead 'fuck yeah']
                , how you doing?[no pause but mentally say 'baby']</li>
              <li>Repeat 5 times, change the sentence and continue</li>
            </ul>
          </li>
        </ul>
      </div>
    `;
  }
}
// Register the new element with the browser.
customElements.define("game-d", GameD);
