export default class CookieUtils {
  static map(string,sep1,sep2) {
    let ret = {}
    string.split(sep1).forEach(it=>ret[it.split(sep2)[0]]=it.split(sep2)[1])
    return ret
  }
  static unmap(hash,sep1,sep2) {
    let ret = ""
    Object.entries(hash).forEach(it=>ret+=it[0]+sep2+it[1]+sep1)
    return ret.slice(0,-1)
  }

}
