import { LitElement, html, css } from "lit-element";

import "@/components/lit/content-selected.js";
import 'lit-toast/lit-toast.js';

import CookieUtils from './cookie-utils.js';

class InputA extends LitElement {
  static get properties() {
    return {
      date: { type: String },
      inputedOne: { type: String },
      inputedTwo: { type: String },
      inputedThree: { type: String },
      inputedFour: { type: String },
      inputedFive: { type: String },
    };
  }

  constructor() {
    super();
    const init = ()=> this.date="" 
    if (document.cookie != null 
          && document.cookie != ""){  
        let hash = CookieUtils.map(document.cookie,':','=')
        if ("game-a" in hash){
          let vals = hash["game-a"].split("|")
          this.date = vals[0]
          this.inputedOne = vals[1]
          this.inputedTwo = vals[2]
          this.inputedThree = vals[3]
          this.inputedFour = vals[4]
          this.inputedFive = vals[5]
        }else
          init()
      }else
        init()      
  }

  static get styles() {
    return [css`
      input{
        margin: 10px;
        margin-left: 25px;
      }
      `,];
  }

  render() {
    return html`
    ${this.date !="" ?
      html`
    Stored at <input type="text" id="date" readonly value="${this.date}"></br>
    <input type="text" id="one" value="${this.inputedOne}"></br>
    <input type="text" id="two" value="${this.inputedTwo}"></br>
    <input type="text" id="three" value="${this.inputedThree}"></br>
    <input type="text" id="four" value="${this.inputedFour}"></br>
    <input type="text" id="five" value="${this.inputedFive}">` 
    : html`
    <input type="text" id="one"></br>
    <input type="text" id="two"></br>
    <input type="text" id="three"></br>
    <input type="text" id="four"></br>
    <input type="text" id="five">`}
    <button @click="${this.store}">Store answers</button>
    <lit-toast></lit-toast>
    `
  }
  store(){
    let f = new Date();
    let month = f.getMonth()
    month++;
    let toStore = f.getDate() + "-"+month+ "-" +f.getFullYear();
    toStore += "|"+this.shadowRoot.querySelector("input#one").value
    toStore += "|"+this.shadowRoot.querySelector("input#two").value
    toStore += "|"+this.shadowRoot.querySelector("input#three").value
    toStore += "|"+this.shadowRoot.querySelector("input#four").value
    toStore += "|"+this.shadowRoot.querySelector("input#five").value

    
    let hash = CookieUtils.map(document.cookie,':','=')
    hash["game-a"]=toStore;
    document.cookie = CookieUtils.unmap(hash,':','=')
    this.shadowRoot.querySelector('lit-toast').show("Stored correctly", 1500);
  }
}
// Register the new element with the browser.
customElements.define("input-a", InputA);
