import { LitElement, html, css } from "lit-element";


class GameI extends LitElement {
  static get properties() {
    return {
    };
  }

  constructor() {
    super();
  }

  static get styles() {
    return [css`
      a{
        font-weight: bolder;
        color: white;
      }
      .my-video{
        display: block;

        min-width: 275px;
        width: 100%;
        min-height: 155px;
        height: 100%;

        margin: 5px auto;
      }  
      @media screen and (min-width: 600px) {
        .my-video{
          width: 90%;
        }
        .video-cont{
          min-height: 205px;
          height: 50vw;
        }
      }
      @media screen and (min-width: 800px) {
        .video-cont{
          max-width: 1074px;
          max-height: 615px;
        }
      }  
    `];
  }

  render() {
    return html`
    <div>
      Sex with me is like</br>
      This game is explined by todd here </br> 
      <div class="video-cont">
        <iframe class="my-video" width="280"
          src="https://www.youtube.com/embed/183envaOYvs?start=374">
        </iframe>
      </div></br></br>
        Now go to <a href="https://random-ize.com/random-youtube/" target="_blank">Random Youtube</a></br>
        Get a random video and do the exercise as Todd Explained!</br>
        Or get some random <a href="https://www.randomlists.com/random-words?dup=false&qty=1" target="_blank">word</a></br>
        Or maybe some random Spanish <a href="https://www.randomlists.com/spanish-words?dup=false&qty=1" target="_blank">word</a></br>
        And remember, DON'T THINK AHEAD
    </div>
  `;
  }
}
// Register the new element with the browser.
customElements.define("game-i", GameI);
